* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Reem Kufi", sans-serif !important;
}

img {
    max-width: 100%;
    height: auto;
}

.App {
    min-height: 100vh;
    width: 85vw;
    max-width: 1900px;
    margin: 0rem auto;
}

body {
    max-width: 100%;
    overflow-x: hidden;
}

body {
    background-color: #f5deb3;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 90px;
}

.navbar-menu-container {
    display: none;
}

.navbar-links-container a {
    margin-right: 3rem;
    text-decoration: none;
    color: rgb(99, 73, 5);
    font-size: 1.5rem;
    font-weight: 600;
    transition: font-size 0.2s ease-in-out 0.05s;
}

.navbar-links-container home_button {
    margin-right: 3rem;
    text-decoration: none;
    color: rgb(99, 73, 5);
    font-size: 1.5rem;
    font-weight: 600;
    transition: font-size 0.2s ease-in-out 0.05s;
}

.navbar-links-container a:hover {
    margin-right: 3rem;
    text-decoration: none;
    color: coral;
    font-size: 1.8rem;
    font-weight: 600;
    text-decoration: underline;
}

.primary-button {
    padding: 0.9rem 1.75rem;
    background-color: whitesmoke;
    color: black;
    outline: none;
    border: none;
    border-radius: 5rem;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 600;
    transition: transform 0.3s ease 0.1s;
}

.navbar-cart-icon {
    font-size: 1.15rem;
    transition: font-size 0.2s ease-in-out 0.1s;
}

.navbar-cart-icon:hover {
    font-size: 2rem;
}

.primary-button:hover {
    background-color: coral;
    color: whitesmoke;
    transform: scale(1.3);
}

.navbar-menu-container svg {
    font-size: 1.5rem;
    cursor: pointer;
}

.home-banner-container {
    position: relative;
    display: flex;
    padding-top: 3rem;
}

.home-bannerImage-container-courses {
    position: absolute;
    top: 115px;
    right: -170px;
    z-index: -2;
    max-width: 700px;
}

.home-bannerImage-container {
    position: absolute;
    top: -90px;
    right: -170px;
    z-index: -2;
    max-width: 700px;
}

.home-image-section {
    max-width: 600px;
    flex: 1;
}

.home-text-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.primary-heading {
    font-size: clamp(2rem, 5vw, 3.5rem);
    color: #4c4c4c;
    /* line-height: 5rem; */
    max-width: 600px;
    text-align: center;
}

.primary-text {
    font-size: clamp(1rem, 3vw, 1.5rem);
    max-width: 500px;
    color: #6a6a6a;
    margin: 1.5rem;
    text-align: center;
    margin-left: 50px;
}

.secondary-button {
    padding: 1rem 2.5rem;
    background-color: #fe9e0d;
    outline: none;
    border: none;
    border-radius: 5rem;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 600;
    color: white;
    transition: 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease 0.1s;
}

.secondary-button svg {
    margin-left: 0.75rem;
    font-size: 1.5rem;
}

.secondary-button:hover {
    background-color: coral;
    transform: scale(1.3);
}

.secondary-button_about {
    padding: 1rem 2.5rem;
    background-color: #fe9e0d;
    outline: none;
    border: none;
    border-radius: 5rem;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 600;
    color: white;
    transition: 0.2s;
