    }
    .home-bannerImage-container {
        max-width: 450px;
    }
    .home-banner-container,
    .about-section-container {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
    }
    .about-section-container {
        flex-direction: column;
    }
    .home-image-section,
    .primary-heading {
        text-align: center;
