    }
    .contact-form-container {
        padding: 0.5rem !important;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 1rem !important;
    }
    .contact-form-container input {
        font-size: 1rem !important;
        max-width: 100%;
        padding: 0.5rem 1rem !important;
        margin-bottom: 0.8rem !important;
        text-align: center;
    }
    .footer-wrapper {
        flex-direction: column;
    }
    .footer-section-two {
        flex-direction: column;
        margin-top: 2rem;
    }
    .footer-section-columns {
        margin: 1rem 0rem;
    }
    .App {
        max-width: 95vw;
    }
}

@media (max-width: 600px) {
    .home-bannerImage-container,
    .about-background-image-container {
        display: none;
    }
}

.course_block {
    width: calc(25% - 20px);
    top: 100px;
    left: 100px;
    bottom: 100px;
    display: inline-block;
    vertical-align: top;
    margin: 50px 50px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .3);
    color: rgba(0, 0, 0, .6);
    transition: .8s;
    text-decoration: none;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
}

.course_block:hover .course_image>img {
    filter: blur(4px);
    -webkit-filter: blur(4px);
}

.course_block:nth-child(3n),
.course_block:nth-child(3n+1) {
    margin: 50px 50px;
}

.course_block img {
    width: 100%;
    transition: .8s
}

.course_details {
    padding: 10px 15px;
}

.course_details p {
    max-height: 38px;
    overflow: hidden
}

.course_block h3 {
    font-size: 16px;
    margin: 0 0 10px 0;
    text-align: center;
}

.course_block p {
    font-size: 14px;
    margin: 0;
}

.course_image {
    position: relative;
    overflow: hidden;
}

.course_date {
    position: absolute;
    bottom: 18px;
    right: 12px;
    background: rgba(0, 0, 0, .6);
    font-size: 13px;
    padding: 4px 8px;
    border-radius: 3px;
    color: #fff;
}

.course_top_notices {
    position: absolute;
    top: 10px;
    right: 12px;
}

.course_new,
.course_unique {
    font-size: 12px;
    display: inline-block;
    padding: 2px 5px;
    font-weight: bold;
    border-radius: 3px;
    margin-left: 5px;
}

.course_unique,
.course_new {
    background: white;
}

.view_course {
    position: absolute;
    top: 40%;
    left: calc(50% - 70px);
    width: 140px;
    text-align: center;
    background: rgba(0, 0, 0, .1);
    box-sizing: border-box;
    padding: 8px 10px;
    border-radius: 5px;
    border: 1px solid #fff;
    color: #fff;
    transition: .8s;
    opacity: 0
}

.course_block:hover .view_course {
    opacity: 1
}

.courses-heading {
    margin-left: 650px;
    text-decoration: underline;
    color: #6a6a6a;
}


/* Heading Animated */

.heading {
    color: #00467d;
    font-weight: bold;
    text-transform: uppercase;
    transform: matrix(1.00, -0.25, 0.00, 1.00, 0, 0);
    -ms-transform: matrix(1.00, -0.25, 0.00, 1.00, 0, 0);
    -webkit-transform: matrix(1.00, -0.25, 0.00, 1.00, 0, 0);
}

#container:before {
    content: " ";
    -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    background-color: rgba(0, 0, 0, 0.085);
    width: 500px;
    height: 750px;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    animation: scale-up-center 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@keyframes scale-up-center {
    0% {
        transform: scale(0.3) matrix(1.00, -0.25, 0.00, 1.00, 0, 0);
    }
    100% {
        transform: scale(1) matrix(1.00, -0.25, 0.00, 1.00, 0, 0);
    }
}

.heading {
    font-size: 3.7em;
    text-shadow: 5px 5px #FF9800, 8px 8px rgba(0, 0, 0, .25);
    animation: slide-tr 1s cubic-bezier(0.250, 0.460, 0.450, 0.940);
    margin-left: 550px;
    margin-top: -20px;
    margin-bottom: 50px;
    text-decoration: underline;
}

@keyframes slide-tr {
    0% {
        transform: translateY(35px) translateX(-150px) matrix(1.00, -0.25, 0.00, 1.00, 0, 0);
    }
    100% {
        transform: translateY(0) translateX(0) matrix(1.00, -0.25, 0.00, 1.00, 0, 0);
    }
}


